<template>
  <div>
    <img class="img" src="../assets/img.jpg" alt="">
    <!-- <img class="img" src="../assets/img2.jpg" alt=""> -->
    <div>
      <div class="form-header item">门店活动</div>
      <div class="item">
        <label for="">门店名称：</label>
        <span>户县森泉汽修厂</span>
      </div>
      <div class="item">
        <label for="">门店电话：</label>
        <span>029-84888854</span>
      </div>
    </div>
    <div>
      <div class="form-header item">填写车牌领取礼品兑换券：</div>
      <form action="">
        <div class="input_control">
          <label for="" class="label">车牌号：</label>
          <input type="text" placeholder="请输入车牌号" v-model="chepaihao">
        </div>
        <div class="input_control">
          <label for="" class="label">手机号：</label>
          <input type="text" class="" placeholder="请输入手机号" v-model="shoujihao">
        </div>
        <button class="btn" @click="submit">活动报名</button>
      </form>
    </div>
    <table class="table">
      <tr>
        <th>序号</th>
        <th>车牌号</th>
        <th v-if="isShowShoujiaohao">手机号</th>
        <th v-if="!isShowShoujiaohao">领取状态</th>
        <th>登记时间</th>
      </tr>
      <tr v-for="(item, index) in dataList" :key=index>
        <td>{{index + 1}}</td>
        <td>{{item.chepaihao}}</td>
        <td v-if="isShowShoujiaohao">{{item.shoujihao}}</td>
        <td v-if="!isShowShoujiaohao">{{item.state}}</td>
        <td>{{item.time}}</td>
      </tr>
    </table>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      chepaihao: '陕',
      shoujihao: '1',
      dataList: [],
      isShowShoujiaohao: false
    }
  },
  created(){
    this.isShowShoujiaohao = false;
     this.getList('');
  },
  filters: {
    formatDate(time) {
      // time = time * 1000
      let date = new Date(time);
      console.log(new Date(time));
      return formatDate(date, 'yyyy-MM-dd hh:mm');
    }
  },
  methods: {
    getList(param){
      var vm = this;
      this.axios.get('/'+param).then(data => {
        let newdata = data.data.data;
        this.dataList = newdata.slice(0, 100);
        this.dataList.forEach(item => {
          item.time = this.formatDate(new Date(item.date), 'yyyy-MM-dd hh:mm');
        })
      }).catch(function (response) {
            console.log(response)
        })
    },
    submit(){
      if (this.chepaihao == 'aaaaaa' && this.shoujihao == 'abc.1234') {
        this.isShowShoujiaohao = true;
        this.getList('allNameAndPhone');
        return;
      } else {
        this.isShowShoujiaohao = false;
      }
      let chepaihaoRex = /^[陕]{1}[A-Z]{1}[A-Z0-9]{4}[A-Z0-9挂学警港澳]{1}$/;
      let shoujihaoRex = /^1[3456789]\d{9}$/;
      let msg = '';
      if (this.chepaihao.length === 7 && chepaihaoRex.test(this.chepaihao)) {

      } else {
        msg = '请输入正确的车牌号'
      }
      if (!shoujihaoRex.test(this.shoujihao)) {
        msg = '请输入正确的手机号'
      }
      if (msg) {
        console.log(msg);
        alert(msg);
        return;
      }
      let data = {
        chepaihao: this.chepaihao,
        shoujihao: this.shoujihao,
      }
      this.axios.post('/', data).then(data => {
        // let newdata = data.data.data;
        // this.dataList = newdata.slice(0, 100);
        if (data.data == '该车牌号已存在') {
          alert('该车牌号已存在');
          return;
        }
        this.getList('');
      }).catch(function (response) {
            console.log(response)
        })
      console.log(data);
    },
    formatDate(date, fmt) {
      if (/(y+)/.test(fmt)) {
        fmt = fmt.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length))
      }
      let o = {
        'M+': date.getMonth() + 1,
        'd+': date.getDate(),
        'h+': date.getHours(),
        'm+': date.getMinutes(),
        's+': date.getSeconds()
      };
      for (let k in o) {
        if (new RegExp(`(${k})`).test(fmt)) {
          let str = o[k] + '';
          fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? str : this.padLeftZero(str));
        }
      }
      return fmt
    },
    padLeftZero(str) {
      return ('00' + str).substr(str.length);
    }
  }
}
</script>

<style scoped>
.img {
  width: 100%
}
.form-header {
  color: #737070;
  font-size: 20px;
  font-weight: bold;
  margin-top: 15px;
}
.item {
  text-align: left;
  height: 50px;
  line-height: 50px;
  border-bottom: solid #ddd 1px;
  padding: 0 10px;
}
.input_control{
  width:auto;
  display: flex;
  align-items: center;
}
.label {
  width: 90px;
  position: absolute;
}
input[type="text"],#btn1,#btn2{
  padding-left: 80px;
  padding-top: 3px;
  box-sizing: border-box;
  font-size:18px;
  height:50px;
  border: none;
  border-bottom:1px solid #c8cccf;
  color:#f25759;
  -web-kit-appearance:none;
  -moz-appearance: none;
  display:block;
  outline:0;
  text-decoration:none;
  width:100%;
}
input[type="text"]:focus{
  border: none;
  border-bottom:1px solid #ff7496;
}
.btn {
  width: 100%;
  height: 50px;
  background: #f25759;
  border: 1px solid #f25759;
  border-radius: 5px;
  color: #fff;
  margin: 10px 0 20px;
}
input::placeholder{
color:#f25759;
}
.table {
  width: 100%
}
.table tr {
  border-top:1px solid #F00;
  border-bottom:1px solid #F00;
} 
.table th,.table td {
  padding: 5px 0;
  border-bottom:1px solid #F00;
} 
</style>



